import GameObject from '../GameObject';
import ImageRepository from '../../Utils/ImageRepository';
import ObjectCreator from '../ObjectCreator';

class Gem extends GameObject{
    constructor(proto) {
        super();
        this.spriteoffset = {'x':96, 'y':32};
        this.hitbox={
            top:9,
            left:10,
            height:15,
            width:12,
        };
        this.value = 100;
        this.offset = 0;
        this.floating=0.2;

        if (proto.value){
            this.value = proto.value;
            if (this.value == 1000){
                this.spriteoffset.x += 32;
                this.hitbox={top:8, left:8, height:16, width:16,};
            }
        }
    }

    load(){
        this.sprite = ImageRepository.get('img/items.png');
    }
    process(){
        this.offset += this.floating;
        this.coords.y += this.floating;

        if (this.offset > 2 || this.offset < -2 ){
            this.floating *= -1;
        }
    }
    gothru(character, direction){
        window.env.game.score += this.value;
        var obj = ObjectCreator.createObjet({type:'flyingtext'}, this.coords.x + 16, this.coords.y);
        obj.text = this.value;
        this.remove();
    }
}

export default Gem;


