import GameObject from '../GameObject';

class Particle extends GameObject{
    constructor() {
        super();
        this.hitbox={
            top:0,
            left:0,
            height:1,
            width:1,
        };
        this.age = 0;
        this.maxage = 300;
        this.movement = {x:0, y:0};
        this.gravity = 0.3;
    }

    load(){}

    process(){
        this.age ++;
        if (this.age > this.maxage){
            this.remove();
        }
        else{
            this.move();
        }
    }

    event(event){
        switch (event.type){
            case 'bump':
                this.remove();
        }
    }

    gothru(character, direction){}

    draw(context){
        var display_x = this.coords.x;
        var display_y = this.coords.y;

        context.fillStyle = 'rgba(255, 0, 0, 0.8)';

        context.fillRect(display_x - window.env.gui.display.offset_x, display_y - window.env.gui.display.offset_y, this.hitbox.width, this.hitbox.height);
    }
};

export default Particle;


