import InputComponent from './InputComponent';

function AiInputComponent(object){
    InputComponent.call(this, object);
    this.goLeft = false;
    this.goRight = false;
    this.goUp = false;
    this.goDown = false;

    this.direction = null;
    this.jump = false;
}
AiInputComponent.prototype = new InputComponent();
AiInputComponent.prototype.process = function(){
    if (this.object.status.type == 'disabled' || this.object.status.type == 'dead'){
        this.goLeft = false;
        this.goRight = false;
        this.goUp = false;
        this.goDown = false;
    }
    else{
        var rand = Math.random();
        if (this.direction == null){
            if (rand >= 0.98){
                this.direction = 'left';
                this.goLeft = true;
            }
            else if (rand >= 0.96){
                this.direction = 'right';
                this.goRight = true;
            }
        }
        else{
            if (rand >= 0.99){
                this.direction = null;
                this.goLeft = false;
                this.goRight = false;
            }
        }

        rand = Math.random();
        if (!this.jump && this.object.canjump){
            if (rand >= 0.99){
                this.jump = true;
                this.goUp = true;
            }
        }
        else if (this.jump){
            if (rand >= 0.90){
                this.jump = false;
                this.goUp = false;
            }
        }
    }
}

export default AiInputComponent;


