import GameObject from '../GameObject';
import ImageRepository from '../../Utils/ImageRepository';
import ObjectCreator from '../ObjectCreator';
import AiInputComponent from '../../Utils/AiInputComponent';

class Clone extends GameObject {

    constructor(proto) {
        super();
        this.spriteoffset = {'x':0, 'y':0};
        this.hitbox={
            top:10,
            left:11,
            height:22,
            width:12,
        };
        this.frame=0;
        this.animation_step=0;
        this.movement = {'x':0,'y':0};
        this.direction = 'right';
        this.looksback = false;
        this.lastswitch = new Date().getTime();
        this.lastshot = new Date().getTime();
        this.shooting = false;
        this.walks = proto.move;
        if (proto.jumps != undefined && proto.jumps){
          this.jumps = true;
        }
        this.status = {
          type:'normal',
          time:new Date().getTime(),
        };
        this.input = new AiInputComponent(this);

        this.direction = proto.direction;
        if (this.walks){
            if (this.direction == 'left'){
                this.movement.x = -1;
                this.spriteoffset.y = 224;
            }
            else if (this.direction == 'right'){
                this.movement.x = 1;
                this.spriteoffset.y = 192;
            }
        }
        else{
            if (this.direction == 'left'){
                this.spriteoffset.y = 32;
            }
        }
    }


    load() {
        this.sprite = ImageRepository.get('img/clone.png');
    }

    process() {
        var time = new Date().getTime();

        this.input.process();

        if (this.status.type != 'disabled' && this.status.type != 'dead'){
            if (this.walks && this.input.goLeft){
                this.movement.x = -1;
            }
            else if (this.walks && this.input.goRight){
                this.movement.x = 1;
            }
            else{
                this.movement.x = 0;
            }

            if (this.movement.x > 0){
                this.spriteoffset.y = 192;
                this.direction = 'right';
            }
            else if (this.movement.x < 0){
                this.spriteoffset.y = 224;
                this.direction = 'left';
            }

            if (this.jumps) {
              if (this.input.goUp && this.canjump){
                  window.env.gui.audio.playSound('audio/Jump.ogg');
                  this.movement.y = -10;
                  this.canjump = false;
              }

              if (this.input.goUp && this.movement.y <= 0){
                  this.movement.y = Math.min(15, this.movement.y + 0.4);
              }
              else{
                  this.movement.y = Math.min(15, this.movement.y + 1);
              }
            }
        }
        else{
            this.movement.x = 0;
            this.movement.y = Math.min(15, this.movement.y + 1);
        }

        this.move();

        if (!this.shooting){
            //this.move();

            if (this.movement.x != 0){
              this.animation_step = (this.animation_step + 1) % 24;
              this.frame = Math.floor(this.animation_step / 8);
              this.spriteoffset.x = this.frame * this.spritesize.x;
            }
            /*else if ((Math.random() >= 0.90 && this.looksback && this.lastswitch + 1000 < time) || (Math.random() >= 0.95 && !this.looksback  && this.lastswitch + 2000 < time)){
                if (this.direction == 'right'){
                    this.direction = 'left';
                    this.spriteoffset.y = 32;
                }
                else{
                    this.direction = 'right';
                    this.spriteoffset.y = 0;
                }
                this.lastswitch = new Date().getTime();
                this.looksback = !this.looksback;
            }
            */
        }
        else{
            if (this.lastshot + 300 < time){
                this.shooting = false;
                this.spriteoffset.x = 0;
                if (this.movement.x > 0){
                    this.spriteoffset.y = 192;
                }
                else if (this.movement.x < 0){
                    this.spriteoffset.y = 224;
                }
                else if (this.direction == 'right'){
                    this.spriteoffset.y = 0;
                }
                else{
                    this.spriteoffset.y = 32;
                }
            }
        }

        if (window.env.game.current_screen.player != undefined){
            if (isFacing(this, window.env.game.current_screen.player.character)){
                this.shoot();
            }
        }
    
    }

    event(event) {
        switch (event.type){
            case 'bump':
                this.movement.x *= -1;
                if (this.movement.x > 0){
                    this.spriteoffset.y = 192;
                    this.direction = 'right';
                }
                else{
                    this.spriteoffset.y = 224;
                    this.direction = 'left';
                }
                break;
            default:
                //Do nothing
                break;
        }
    }

    gothru(character, direction) {
        character.movement.y *= -0.5;
        character.movement.x *= -1;
        character.loselife();
    }

    shoot() {
        time = new Date().getTime();
        if (this.lastshot + 1000 < time){
            let obj;
            this.shooting = true;
            this.spriteoffset.x = 32;
            if (this.direction == 'left'){
                bullet = ObjectCreator.createObjet({type:'bullet'}, this.coords.x, this.coords.y + 16);
                bullet.movement.x = -3;
                bullet.spriteoffset.y = 3;
                this.spriteoffset.y = 32;
                obj = ObjectCreator.createObjet({type:'flyingtext'}, this.coords.x, this.coords.y + 16);
                obj.font = 'bold 10px monospace';
                obj.text = 'PEW';
            }
            else{
                bullet = ObjectCreator.createObjet({type:'bullet'}, this.coords.x + 32, this.coords.y + 16);
                bullet.movement.x = 3;
                bullet.spriteoffset.y = 0;
                this.spriteoffset.y = 0;
                obj = ObjectCreator.createObjet({type:'flyingtext'}, this.coords.x + 32, this.coords.y + 16);
                obj.font = 'bold 10px monospace';
                obj.text = 'PEW';
            }

            this.lastshot = new Date().getTime();
        }
    }
};

export default Clone;


