import ImageResource from './Resource/ImageResource';
import SoundResource from './Resource/SoundResource';
import ImageRepository from './ImageRepository';

class Loader {
  constructor(){
    this._resources = [];
    this._loaded = 0;
    this._total = 0;
  }

  preload(resources) {
    for (var i in resources){
      this._total += resources[i].length;
    }
    if (resources.images != undefined && resources.images.length > 0){
      for (var i in resources.images){
        var resource = new ImageResource(resources.images[i], this);
        this._resources.push(resource);
      }
    }
    if (resources.audio != undefined && resources.audio.length > 0){
      for (var i in resources.audio){
        var resource = new SoundResource(resources.audio[i], this);
        this._resources.push(resource);
      }
    }
  };
  resourceLoaded() {
      this._loaded++;
      if (this.getPercentLoaded() == 100){
        this.onload();
      }
  };
  getLoaded() {
    return this._loaded;
  };
  getTotal() {
    return this._total;
  };
  getPercentLoaded() {
    if (this._resources.length > 0){
      return Math.floor((this._loaded / this._total) * 100);
    }
    else{
      return 0;
    }
  };
  onload(){} ;
}

export default Loader;



