import Menu from '../classes/Screens/Menu';
import ControlMenu from '../classes/Screens/Menu/ControlMenu';
import Level from '../classes/Screens/Level';
import KeyBoardManager from '../classes/Utils/Input/KeyBoardManager';

var menus = {
  main: {
    title:'Main menu',
    music:'A Friendly Encounter.mp3',
    items: [
      {
        title: '1 Player',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Level();
          env.game.current_screen.loadSingle(0);
          env.game.current_screen.start();
        }
      },
      {
        title: '2 Players',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Level();
          env.game.current_screen.loadMulti(0);
          env.game.current_screen.start();
        }
      },
      {
        title: 'High Scores',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['highscores']);
          env.game.current_screen.start();
        }
      },
      {
        title: 'Options',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['options']);
          env.game.current_screen.start();
        }
      },
    ]
  },
  options: {
    title:'Options',
    items: [
      {
        title: 'Player name',
        callback: function(){
          this.title = 'Player name: _';
          env.config.setVariable('player.name', '');
          KeyBoardManager.reset();
          this.takeFocus();
        },
        onLoad: function(){
          this.title = 'Player name: '+env.config.variable('player.name');
        },
        onAction: function(key){
          if (key == KeyBoardManager.ENTER && env.config.variable('player.name')!=''){
            this.title = 'Player name: '+env.config.variable('player.name');
            env.config.save();
            this.removeFocus();
          }
          else if (
            (key >= 65 && key <= 90)
            || (key >= 96 && key <= 105)
          ){
            if (KeyBoardManager.isDown(KeyBoardManager.SHIFT)){
              env.config.setVariable('player.name', env.config.variable('player.name') + KeyBoardManager.keys[key]);
            }
            else{
              env.config.setVariable('player.name', env.config.variable('player.name') + KeyBoardManager.keys[key].toLowerCase());
            }
            
            this.title = 'Player name: '+env.config.variable('player.name')+'_';
          }
          else if (key == KeyBoardManager.SPACE){
            env.config.setVariable('player.name', env.config.variable('player.name') + ' ');
            this.title = 'Player name: '+env.config.variable('player.name')+'_';
          }
          else if (key == KeyBoardManager.BACKSPACE){
            env.config.setVariable('player.name', env.config.variable('player.name').substr(0, env.config.variable('player.name').length - 1));
            this.title = 'Player name: '+env.config.variable('player.name')+'_';
          }
        }
      },
      {
        title: 'Controls Player 1',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['controls1']);
          env.game.current_screen.start();
        }
      },
      {
        title: 'Controls Player 2',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['controls2']);
          env.game.current_screen.start();
        }
      },
      {
        title: 'Audio',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['audio']);
          env.game.current_screen.start();
        }
      },
      {
        title: 'Back',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['main']);
          env.game.current_screen.start();
        }
      },
    ]
  },
  gameover: {
    title:'Continue?',
    items: [
      {
        title: 'Continue',
        callback: function(){
            env.game.current_screen.stop();
            env.game.current_screen = new Level();
            env.game.current_screen.load(0);
            env.game.current_screen.start();
        }
      },
      {
        title: 'Exit to main menu',
        callback: function(){
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus.main);
          env.game.current_screen.init();
          env.game.current_screen.start();
        }
      },
    ]
  },
  controls1: new ControlMenu(0),
  controls2: new ControlMenu(1),
  audio: {
    title:'Audio',
    items: [
      {
        title: 'Music volume',
        callback: function(){
          this.title = 'Music volume: -'+env.config.values.audio.musicVolume+'-%';
          KeyBoardManager.reset();
          this.takeFocus();
        },
        onLoad: function(){
          this.title = 'Music volume: '+env.config.values.audio.musicVolume+'%';
        },
        onAction: function(key){
          if (key == KeyBoardManager.ENTER && env.config.values.player.name!=''){
            this.title = 'Music volume: '+env.config.values.audio.musicVolume+'%';
            env.config.save();
            this.removeFocus();
          }
          else if (key == KeyBoardManager.LEFT || key == KeyBoardManager.DOWN){
            if (env.config.values.audio.musicVolume > 0){
              env.config.values.audio.musicVolume -= 10;
              env.gui.audio.volume.music.gain.value = env.config.values.audio.musicVolume / 50 - 1;
            }
            this.title = 'Music volume: -'+env.config.values.audio.musicVolume+'-%';
          }
          else if (key == KeyBoardManager.UP || key == KeyBoardManager.RIGHT){
            if (env.config.values.audio.musicVolume < 100){
              env.config.values.audio.musicVolume += 10;
              env.gui.audio.volume.music.gain.value = env.config.values.audio.musicVolume / 50 - 1;
            }
            this.title = 'Music volume: -'+env.config.values.audio.musicVolume+'-%';
          }
        }
      },
      {
        title: 'Sound volume',
        callback: function(){
          this.title = 'Sound volume: -'+env.config.values.audio.soundVolume+'-%';
          KeyBoardManager.reset();
          this.takeFocus();
        },
        onLoad: function(){
          this.title = 'Sound volume: '+env.config.values.audio.soundVolume+'%';
        },
        onAction: function(key){
          if (key == KeyBoardManager.ENTER && env.config.values.player.name!=''){
            this.title = 'Sound volume: '+env.config.values.audio.soundVolume+'%';
            env.config.save();
            this.removeFocus();
          }
          else if (key == KeyBoardManager.LEFT || key == KeyBoardManager.DOWN){
            if (env.config.values.audio.soundVolume > 0){
              env.config.values.audio.soundVolume -= 10;
              env.gui.audio.volume.sound.gain.value = env.config.values.audio.soundVolume / 50 - 1;
            }
            this.title = 'Sound volume: -'+env.config.values.audio.soundVolume+'-%';
          }
          else if (key == KeyBoardManager.UP || key == KeyBoardManager.RIGHT){
            if (env.config.values.audio.soundVolume < 100){
              env.config.values.audio.soundVolume += 10;
              env.gui.audio.volume.sound.gain.value = env.config.values.audio.soundVolume / 50 - 1;
            }
            this.title = 'Sound volume: -'+env.config.values.audio.soundVolume+'-%';
          }
        }
      },
      {
        title: 'Save Changes',
        callback: function(){
          env.config.save();
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['options']);
          env.game.current_screen.start();
        }
      },
      {
        title: 'Cancel',
        callback: function(){
          env.config.load();
          env.gui.audio.volume.music.gain.value = env.config.values.audio.musicVolume / 50 - 1;
          env.gui.audio.volume.sound.gain.value = env.config.values.audio.soundVolume / 50 - 1;
          env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['options']);
          env.game.current_screen.start();
        }
      },
    ]
  },
  highscores: {
    title:'High Scores',
    items:[
      {
        title: 'Back',
        callback: function(){
            env.game.current_screen.stop();
          env.game.current_screen = new Menu();
          env.game.current_screen.load(menus['main']);
          env.game.current_screen.start();
        }
      },
    ],
    afterDraw:function(context){
      for (var i in env.highscores){
        context.fillText(env.highscores[i].name, env.gui.display.width / 2 - 48, 140 + i*16);
        context.fillText(env.highscores[i].score, env.gui.display.width / 2 + 48, 140 + i*16);
      }
    }
  }
}

export default menus;


