import ConfigManager from './Utils/ConfigManager';
import Gui from './Utils/Gui';
import Menu from './Screens/Menu';
import menus from '../data/menus';
import KeyBoardManager from './Utils/Input/KeyBoardManager';

function Game(){
  this.loop = 0;
  this.score = 0;
  this.paused = false;
  this.current_screen = {};
  this.skipped_frames = 0;
};
Game.prototype = {
  init:function(){
    var game = this;

    if (!localStorage.getItem('maxlevel')){
      localStorage.setItem('maxlevel', 1);
    }
    if (localStorage.getItem('highscores')){
      window.env.highscores=JSON.parse(localStorage.getItem('highscores'));
    }
    window.env.config.load();
    this.current_screen = new Menu();
    this.current_screen.load(menus['main']);

    window.env.gui.init();
    this.current_screen.start();
  }
}

window.env = {
    config:new ConfigManager(),
    highscores:[],
    game:null,
    gui:new Gui(),
    init:function(){
      this.game = new Game();
      this.gui = new Gui();
      this.game.init();
    }
};



window.addEventListener('keyup', function(event) { KeyBoardManager.onKeyup(event); }, false);
window.addEventListener('keydown', function(event) { KeyBoardManager.onKeydown(event); }, false);
//TODO: onkeypress
var stop = false;

window.onload = function(){window.env.init();};



