import InputComponent from './InputComponent';
import KeyBoardManager from './Input/KeyBoardManager';

function PlayerInputComponent(object, player){
    InputComponent.call(this, object);
    this.player = player;
    this.goLeft = false;
    this.goRight = false;
    this.goUp = false;
    this.goDown = false;
}
PlayerInputComponent.prototype = new InputComponent();
PlayerInputComponent.prototype.process = function(){
    if (this.player.status.type == 'disabled' || this.player.status.type == 'dead'){
        this.goLeft = false;
        this.goRight = false;
        this.goUp = false;
        this.goDown = false;
    }
    else{
        this.goLeft = KeyBoardManager.isDown(this.player.keys.left);
        this.goRight = KeyBoardManager.isDown(this.player.keys.right);
        this.goUp = KeyBoardManager.isDown(this.player.keys.up);
        //this.goDown = KeyBoardManager.isDown(this.player.keys.down);;
    }
}

export default PlayerInputComponent;


