import KeyBoardManager from './Input/KeyBoardManager';

function ConfigManager(){
    this.values = {
        players : [
          {
            name:'Player 1',
            keys:{
                left:KeyBoardManager.LEFT,
                right:KeyBoardManager.RIGHT,
                up:KeyBoardManager.UP,
                down:KeyBoardManager.DOWN,
            }
          },
          {
            name:'Player 2',
            keys:{
                left:KeyBoardManager.Q,
                right:KeyBoardManager.D,
                up:KeyBoardManager.Z,
                down:KeyBoardManager.S,
            }
          },
        ],
        audio : {
            musicVolume:50,
            soundVolume:50,
        },
    }
}

ConfigManager.prototype={
    save:function(){
        localStorage.setItem('config', JSON.stringify(this.values));
    },
    load:function(){
        if (localStorage.getItem('config')){
            var config = JSON.parse(localStorage.getItem('config'));

            for (var key in config){
                this.values[key] = config[key];
            }
            
        }
    },
    variable:function(key){
        var keys = key.split('.');
        var val = this.values;
        for (var k in keys){
            if (k == keys.length - 1){
                return val[keys[k]];
            }
            if (val[keys[k]] == undefined){
                return undefined;
            }
            val= val[keys[k]];
        }
    },
    setVariable:function(key, value){
        var keys = key.split('.');
        var val = this.values;
        for (var k in keys){
            if (k == keys.length - 1){
                val[keys[k]] = value;
                return true;
            }
            if (val[keys[k]] == undefined){
                val[keys[k]] = {};
            }
            val= val[keys[k]];
        }
        return false;
    }
}

export default ConfigManager;


