import GameObject from '../GameObject';
import ImageRepository from '../../Utils/ImageRepository';

class Bullet extends GameObject {
    constructor() {
        super();
        this.spriteoffset = {x:0, y:0};
        this.hitbox={
            top:0,
            left:1,
            height:3,
            width:4,
        };
        this.spritesize = {x:6, y:3};
        this.movement = {x:0, y:0};
        this.fall = false;
    }

    load() {
        this.sprite = ImageRepository.get('img/bullet.png');
    }

    process() {
        this.move();
    }

    event(event) {
        switch (event.type){
            case 'bump':
                this.remove();
        }
    }

    gothru(character, direction) {
        character.movement.y *= -0.5;
        character.movement.x *= -1;
        character.loselife();
        this.remove();
    }
};

export default Bullet;


