import Resource from './Resource';

function SoundResource(url, loader){
  Resource.call(this);

  var thatResource = this;
  this.loader = loader;
  this.type = 'sound';
  this.loaded = false;
  this.url = url;

  var request = new XMLHttpRequest();
  request.open('GET', url, true);
  request.responseType = 'arraybuffer';

  request.onload = function(){
    window.env.gui.audio.context.decodeAudioData(request.response, 
      function(buffer) {
        window.env.gui.audio.library[thatResource.url] = buffer;
        thatResource.loaded = true;
        thatResource.loader.resourceLoaded();
      }, 
      function(error){
        thatResource.loaded = true;
        thatResource.loader.resourceLoaded();
        console.log(error);
      }
    );
  }
  request.send();
}

export default SoundResource;


